import React from "react";
import { WidthProvider, Responsive } from "react-grid-layout";
import Bee from "./../board/boardsType/bee";
import styled from "styled-components";
import itda from "./../initialData";
import { useState, useEffect } from "react";
import './../App.css';

const ResponsiveReactGridLayout = WidthProvider(Responsive);
const originalLayouts = getFromLS("layouts") || {};

const Container = styled.div`
  background-color: #fff;
  display: flex;
  flex-direction: column;
  //box-shadow: 0 3px 5px -1px #d3d3d3, 0 6px 10px 0 #d3d3d3, 0 1px 18px 0 #d3d3d3;
`;

const WrapperComponent = styled.div`
  overflow-y: auto;
  background-color: white;
  margin-top: 20px
`;

/**
 * This layout demonstrates how to sync multiple responsive layouts to localstorage.
 */
const Grid = (props) => {

  const [layouts, setLayouts] = useState(JSON.parse(JSON.stringify(originalLayouts)))
  const [update, setUpdate] = useState(false)

  const saveBoard = (id, value) => {
    let newData = {
        ...props.data,
        boards: {
            ...props.data.boards,
            [id]: {
                ...props.data.boards[id],
                content: value
            }
        }
    }
    localStorage.setItem("bee", JSON.stringify(newData))
    setUpdate(prev => !prev)
}

const deleteBoard = (id) => {
    let newData = {...props.data}
    let index = newData.boardsOrder.indexOf(id)
    newData.boardsOrder.splice(index, 1)
    delete newData.boards[id]
    localStorage.setItem("bee", JSON.stringify(newData))
    setUpdate(prev => !prev)
}

  function generateDOM() {
    return props.data.boardsOrder.map((boardId, index) => {
      const board = props.data.boards[boardId];

      return (
        <Container key={index} data-grid={{ w: 2, h: 3, x: 0, y: 0, minW: 2, minH: 3}}>
          <WrapperComponent className={"noDrag"}>
            <Bee
              initialData={board.content}
              board={board}
              index={index}
              deleteBoard={deleteBoard}
              saveBoard={saveBoard}
            />
          </WrapperComponent>
        </Container>
      );
    });
  };
 

  function resetLayout() {
    setLayouts({})
  }

  function onLayoutChange(layout, layouts) {
    saveToLS("layouts", layouts);
    setLayouts(layouts)

  }

    return (
      <div>
        {/* <button onClick={() => resetLayout()}>Reset Layout</button> */}
        <ResponsiveReactGridLayout
          className="layout"
          cols={{ lg: 50, md: 10, sm: 6, xs: 4, xxs: 2 }}
          rowHeight={30}
          layouts={layouts}
          draggableCancel=".noDrag"
          onLayoutChange={(layout, layouts) =>
            onLayoutChange(layout, layouts)
          }
          >
          {generateDOM()}
        </ResponsiveReactGridLayout>
      </div>
    );
}

function getFromLS(key) {
  let ls = {};
  if (global.localStorage) {
    try {
      ls = JSON.parse(global.localStorage.getItem("rgl-8")) || {};
    } catch (e) {
      /*Ignore*/
    }
  }
  return ls[key];
}

function saveToLS(key, value) {
  if (global.localStorage) {
    global.localStorage.setItem(
      "rgl-8",
      JSON.stringify({
        [key]: value
      })
    );
  }
}

Grid.defaultProps = {
    className: "layout",
    cols: { lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 },
    rowHeight: 30,
}

export default Grid